// 该文件用于创建整个应用的路由器
import VueRouter from 'vue-router'
// 引入组件
import About from '../pages/About.vue'
import Home from '../pages/Home.vue'
import Message from '../pages/Message.vue'
import News from '../pages/News.vue'
import Detail from '../pages/Detail.vue'

// 创建并暴露一个路由器
const router = new VueRouter({
    routes: [
        {
            name: 'about',
            path: '/about',
            component: About,
            meta: { title: '关于', isAuth: true }
        },
        {
            name: 'home',
            path: '/home',
            component: Home,
            meta: { title: '主页' },
            children: [
                {
                    name: 'news',
                    path: 'news',
                    component: News,
                    meta: { isAuth: true, title: '新闻' },
                    /* beforeEnter: (to, from, next) => {
                        if (to.meta.isAuth) {
                            if (localStorage.getItem('name') === 'Leah') {
                                next()
                            } else {
                                alert('你没有权限进入！')
                            }
                        } else {
                            next()
                        }
                    } */
                },
                {
                    name: 'message',
                    path: 'message',
                    component: Message,
                    meta: { isAuth: true, title: "消息" },
                    children: [
                        {
                            name: 'detail',
                            path: 'detail/:id/:title',
                            component: Detail,
                            // 第一种写法，值为对象，该对象中所有key-value都会以props的形式传给Detail组件
                            // props: {a: 1, b: 'hello'}

                            // 第二种写法,值为布尔值，就会把该路由器组件收到的所有params参数，以props的形式传给Detail组件
                            // props: true

                            // 第三种写法，值为函数
                            /* props($route) {
                                // return {id: 666, title: 123}
                                console.log($route)
                                return {id: $route.query.id, title: $route.query.title}
                            } */
                            // 解构赋值
                            /* props({query}) {
                                // return {id: 666, title: 123}
                                // console.log($route)
                                return {id: query.id, title: query.title}
                            } */
                            // 连续解构赋值
                            props({ query: { id, title } }) {
                                // return {id: 666, title: 123}
                                // console.log($route)
                                return { id, title }
                            }
                        }
                    ]
                }
            ]
        }
    ]
})

/* // 全局前置路由守卫---初始化的时候调用、每次路由切换之前被调用
router.beforeEach((to, from, next) => {
    console.log(to, from)
    // document.title = to.meta.title || 'VueRouter'
    // if(to.path === '/home/news' || to.path === '/home/message') {
    if(to.meta.isAuth) {
        if(localStorage.getItem('name') === 'Leah') {
            next()
        }else {
            alert('你没有权限进入！')
        }
    } else {
        next()
    }
})*/ 

// 全局后置路由守卫---初始化的时候被调用、每次路由切换之后被调用
router.afterEach((to, from) => {
    console.log('后置路由', to, from)
    document.title = to.meta.title || 'VueRouter'
})

export default router