<template>
  <h2>我是About的内容</h2>
</template>

<script>
export default {
    name: 'AboutCom',
    /* mounted() {
      console.log('About组件挂载完毕')
    },
    beforeDestroy() {
      console.log('About组件即将销毁')
    }, */

    // 通过路由规则进入该组件时被调用
    beforeRouteEnter (to, from, next) {
      // ...
      if(to.meta.isAuth) {
        if(localStorage.getItem('name') === "Leah") {
          next()
        } else {
          alert('你的权限不够！')
        }
      }else {
        next()
      }
    },

    // 通过路由规则，离开时该组件被调用
    beforeRouteLeave(to, from, next) {
      // console.log('About--beforeRouterLeave', to, from)
      next()
    }
}
</script>

